/* swiper button Custom CSS */
:root { --swiper-navigation-size: 48px
; }

.swiper-button-next, .swiper-button-prev { position: absolute; top: unset; bottom: 0; width: var(--swiper-navigation-size); height: var(--swiper-navigation-size); margin-top: unset; cursor: pointer; color: var(--swiper-navigation-color, var(--swiper-theme-color)); }

.swiper-button-next:hover::after, .swiper-button-prev:hover::after { background-color: #2405f2; background-image: url("../images/ico_btn_swiper_w.png"); }

.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled { opacity: .35; cursor: auto; pointer-events: none; }

.swiper-button-next:after, .swiper-button-prev:after { font-size: 0; width: var(--swiper-navigation-size); height: var(--swiper-navigation-size); border-radius: var(--swiper-navigation-size); background-color: #ffffff; background-repeat: no-repeat; background-position: center; background-size: 13px 22px; transition: all 0.3s; }

.swiper-button-prev, .swiper-container-rtl .swiper-button-next { left: auto; right: calc(var(--swiper-navigation-size) + 20px); }

.swiper-button-prev:after, .swiper-container-rtl .swiper-button-next:after { background-image: url("../images/ico_btn_swiper_b.png"); transform: rotate(180deg); }

.swiper-button-next, .swiper-container-rtl .swiper-button-prev { right: 10px; left: auto; }

.swiper-button-next:after, .swiper-container-rtl .swiper-button-prev:after { background-image: url("../images/ico_btn_swiper_b.png"); }

